.container{
    background-image: url(../Components/BGSignup.jpg);
    position: fixed;
    min-height: 100%;
    min-width: 100%;
    background-size: cover;
    background-position: center;
}

.form{
    text-align: center;
    background-color: white;
    max-width: 350px;
    width: 100%;
    padding: 100px 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 20px;
    box-shadow: 0 40px 80px 0 rgba(0, 0, 0, 0.27), 0 60px 80px 0 rgba(0, 0, 0, 0.19);
}
.btn{
    background-color: rgb(22, 6, 168);
    color: white;
    border: none;
    padding: 10px 25px;
    border-radius: 7px;
    cursor: pointer;
}
.btn:hover {
    opacity: 0.8;
    transition: all 0.8s ease-in-out;
}

.error{
    color: red;
    text-transform: capitalize;
}
.txtfield{
    width: 75%;
    border: none;
    border-bottom: 2px solid black;
    color: black;
    padding: 10px;
    outline: none;
    text-align: center;
    
}
.txtfield::placeholder{
    color: black;
    text-align: center;
    opacity: 0.5;

}
h2{
    position: absolute;
    top: 5%;
    left: 37%;
}